<template>
<div class="jobs-list">
    <segment class="clearfix" :class="{loading: processing}">
        <div class="ui dividing clearfix header"> <small style="color:#99A9BF" v-if="workerName">{{workerName}}</small>
            <button class="pull-right ui green tiny button" :class="{disabled: !workerId}" @click="activeNewJobDialog = true">
                <i class="icon add"></i>
            </button>
        </div>

        <el-tabs v-model="activeTab">
            <el-tab-pane label="" name="month-list">
                <div class="toolbar">
                    <el-radio-group v-model="month" v-on:change="changeMonth">
                        <el-radio :label="1">1</el-radio>
                        <el-radio :label="2">2</el-radio>
                        <el-radio :label="3">3</el-radio>
                        <el-radio :label="4">4</el-radio>
                        <el-radio :label="5">5</el-radio>
                        <el-radio :label="6">6</el-radio>
                        <el-radio :label="7">7</el-radio>
                        <el-radio :label="8">8</el-radio>
                        <el-radio :label="9">9</el-radio>
                        <el-radio :label="10">10</el-radio>
                        <el-radio :label="11">11</el-radio>
                        <el-radio :label="12">12</el-radio>
                    </el-radio-group>
                </div>

                <table class="ui celled selectable table" v-if="jobs.length > 0">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>

                    <tbody>
                        <tr v-for="job in jobs">
                            <td>{{ job.job_date }}</td>
                            <td>{{ job.name }}</td>
                            <td>{{ job.fee }}</td>
                            <td>{{ job.quantity }}</td>
                            <td>{{ job.amount }}</td>

                            <td class="center aligned">
                                <button class="ui tiny circular teal icon button" @click="editJob(job)">
                                    <i class="icon edit"></i>
                                </button>

                                <button class="ui tiny circular red icon button" @click="removeJob(job)">
                                    <i class="icon remove"></i>
                                </button>
                            </td>
                        </tr>
                    </tbody>

                    <tfoot class="full-width">
                         <tr>
                             <th colspan="5"></th>
                             <th>: <strong>{{ totalAmount }}</strong></th>
                         </tr>
                    </tfoot>
                </table>

                <div class="ui info message" v-else>
                    
                </div>
            </el-tab-pane>

            <el-tab-pane label="" name="year-list">
                <table class="ui celled selectable table" v-if="summary.length > 0">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>

                    <tbody>
                        <tr v-for="item in summary">
                            <td>{{ item.month }}</td>
                            <td>{{ item.amount }}</td>
                        </tr>
                    </tbody>
                </table>

                <div class="ui info message" v-else>
                    
                </div>
            </el-tab-pane>
        </el-tabs>
    </segment>

    <el-dialog title="" v-model="activeNewJobDialog">
        <div class="ui worker form">
            <div class="required field">
                <label></label>

                <select v-model="newJob.productId">
                    <option value="0"></option>
                    <option v-bind:value="product.id" v-for="product of products">
                        {{ product.name }}
                    </option>
                </select>
            </div>

            <div class="required field">
                <label></label>
                <input type="text" v-model="newJob.quantity" placeholder="" />
            </div>

            <div class="required field">
                <label></label>

                <div class="two fields">
                    <div class="required field">
                        <select v-model="newJob.month">
                            <option value="0"></option>
                            <option value="1"></option>
                            <option value="2"></option>
                            <option value="3"></option>
                            <option value="4"></option>
                            <option value="5"></option>
                            <option value="6"></option>
                            <option value="7"></option>
                            <option value="8"></option>
                            <option value="9"></option>
                            <option value="10"></option>
                            <option value="11"></option>
                            <option value="12"></option>
                          </select>
                    </div>

                    <div class="required field">
                        <select v-model="newJob.day">
                            <option value="0"></option>
                            <option value="1">1</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                            <option value="6">6</option>
                            <option value="7">7</option>
                            <option value="8">8</option>
                            <option value="9">9</option>
                            <option value="10">10</option>
                            <option value="11">11</option>
                            <option value="12">12</option>
                            <option value="13">13</option>
                            <option value="14">14</option>
                            <option value="15">15</option>
                            <option value="16">16</option>
                            <option value="17">17</option>
                            <option value="18">18</option>
                            <option value="19">19</option>
                            <option value="20">20</option>
                            <option value="21">21</option>
                            <option value="22">22</option>
                            <option value="23">23</option>
                            <option value="24">24</option>
                            <option value="25">25</option>
                            <option value="26">26</option>
                            <option value="27">27</option>
                            <option value="28">28</option>
                            <option value="29">29</option>
                            <option value="30">30</option>
                            <option value="31">31</option>
                          </select>
                    </div>
                </div>
            </div>
        </div>

        <span slot="footer" class="dialog-footer">
            <button class="ui fluid green button" :class="{loading: addingJob}" @click="addJob()"></button>
        </span>
    </el-dialog>

    <el-dialog title="" v-model="activeEditJobDialog">
        <div class="ui worker form">
            <div class="required field">
                <label></label>

                <select v-model="currentJob.productId">
                    <option value="0"></option>
                    <option v-bind:value="product.id" v-for="product of products">
                        {{ product.name }}
                    </option>
                </select>
            </div>

            <div class="required field">
                <label></label>
                <input type="text" v-model="currentJob.quantity" placeholder="" />
            </div>

            <div class="required field">
                <label></label>

                <div class="two fields">
                    <div class="required field">
                        <select v-model="currentJob.month">
                            <option value="0"></option>
                            <option value="1"></option>
                            <option value="2"></option>
                            <option value="3"></option>
                            <option value="4"></option>
                            <option value="5"></option>
                            <option value="6"></option>
                            <option value="7"></option>
                            <option value="8"></option>
                            <option value="9"></option>
                            <option value="10"></option>
                            <option value="11"></option>
                            <option value="12"></option>
                          </select>
                    </div>

                    <div class="required field">
                        <select v-model="currentJob.day">
                            <option value="0"></option>
                            <option value="1">1</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                            <option value="6">6</option>
                            <option value="7">7</option>
                            <option value="8">8</option>
                            <option value="9">9</option>
                            <option value="10">10</option>
                            <option value="11">11</option>
                            <option value="12">12</option>
                            <option value="13">13</option>
                            <option value="14">14</option>
                            <option value="15">15</option>
                            <option value="16">16</option>
                            <option value="17">17</option>
                            <option value="18">18</option>
                            <option value="19">19</option>
                            <option value="20">20</option>
                            <option value="21">21</option>
                            <option value="22">22</option>
                            <option value="23">23</option>
                            <option value="24">24</option>
                            <option value="25">25</option>
                            <option value="26">26</option>
                            <option value="27">27</option>
                            <option value="28">28</option>
                            <option value="29">29</option>
                            <option value="30">30</option>
                            <option value="31">31</option>
                          </select>
                    </div>
                </div>
            </div>
        </div>

        <span slot="footer" class="dialog-footer">
            <button class="ui fluid green button" :class="{loading: editingJob}" @click="updateJob()"></button>
        </span>
    </el-dialog>
</div>
</template>

<script>
import segment from 'components/Segment';
import pagination from 'components/Pagination';

const props = ['workerId', 'workerName'];

const data = () => {
    return {
        activeTab: 'month-list',
        activeNewJobDialog: false,
        activeEditJobDialog: false,

        jobs: [],
        summary: [],
        month: null,

        products: [],
        newJob: {
            productId: 0,
            quantity: 1,
            month: null,
            day: null
        },

        currentJob: {
            id: 0,
            productId: 0,
            quantity: 1,
            month: null,
            day: null
        },

        addingJob: false,
        editingJob: false,
        processing: false
    }
};

const computed = {
    totalAmount() {
        let amount = 0;

        if (this.jobs.length) {
            for (let job of this.jobs) {
                amount += job.amount;
            }
        }

        return amount;
    }
};

const watch = {
    workerId(id) {
        if (id) {
            this.loadJobs();
        }else {
            this.jobs = [];
            this.summary = [];
        }
    }
};

const methods = {
    changeMonth() {
        if (this.workerId) {
            this.loadJobs();
        }
    },

    loadJobs() {
        this.processing = true;

        this.$http.get('jobs', {
            params: {
                month: this.month,
                workerId: this.workerId
            }
        }).then(response => {
            let res = response.data;

            if (res) {
                this.jobs = res;
            }else {
                this.jobs = [];
            }
        }).finally(() => {
            this.processing = false;
        });
    },

    addJob() {
        this.addingJob = true;

        this.$http.post('job/add', {
            ...this.newJob,
            ...this.currentDate,
            workerId: this.workerId
        }).then(response => {
            if (response.data.success) {
                this.resetNewJob();
                this.loadJobs();
            }
        }).finally(() => {
            this.addingJob = false;
        });
    },

    removeJob(job) {
        this.processing = true;

        this.$http.post('job/remove', {
            id: job.id
        }).then(response => {
            if (response.data.success) {
                this.loadJobs();
            }
        }).finally(() => {
            this.processing = false;
        });
    },

    editJob(job) {
        this.activeEditJobDialog = true;
        this.currentJob.id = job.id;
        this.currentJob.productId = job.product_id;
        this.currentJob.quantity = job.quantity;

        let currentJobDate = this.getDate(job.job_date);
        this.currentJob.month = currentJobDate.month + 1;
        this.currentJob.day = currentJobDate.day;
    },

    updateJob(job) {
        this.editingJob = true;

        this.$http.post('job/update', {
            ...this.currentJob,
            workerId: this.workerId
        }).then(response => {
            if (response.data.success) {
                this.resetEditJob();
                this.loadJobs();
            }
        }).finally(() => {
            this.editingJob = false;
        });
    },

    resetEditJob() {
        this.activeEditJobDialog = false;

        this.currentJob.id = 0;
        this.currentJob.productId = 0;
        this.currentJob.quantity = 1;
        this.currentJob.month = null;
        this.currentJob.day = null;
    },

    resetNewJob() {
        this.activeNewJobDialog = false;
        this.newJob.productId = 0;
        this.newJob.quantity = 1;

        let currentDate = this.getDate();
        this.setNewJobDate(currentDate);
    },

    loadProducts() {
        this.$http.get('products/all').then(response => {
            this.products = response.data;
        });
    },

    getDate(dateString) {
        let d;

        if (dateString) {
            d = new Date(dateString);
        }else {
            d = new Date();
        }

        let month = d.getMonth();
        let day = d.getDate();

        return {
            month,
            day
        };
    },

    setNewJobDate(currentDate) {
        this.newJob.month = currentDate.month + 1;
        this.newJob.day = currentDate.day;
    }
};

const components = {
    segment,
    pagination
};

const mounted = function() {
    this.loadProducts();

    let currentDate = this.getDate();
    this.setNewJobDate(currentDate);
    this.month = currentDate.month + 1;
};

export default {
    name: 'jobs-list',
    props,
    data,
    computed,
    methods,
    watch,
    components,
    mounted
};
</script>

<style lang="less">
    .jobs-list {
        .toolbar {
            padding: 5px;
            font-size:12px;
        }
    }
</style>



